<template>
  <div>
    <div class="bgbox">
      <van-button type="primary" size="small" class="minibtn">{{time}}秒后进入</van-button>
      <img src="../../assets/img/timg.jpg" alt />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      time: 3,
      timeval: null
    };
  },
  methods: {
    goHome() {
      this.$router.push("/home"); //推荐页面
    }
  },
  created() {
    this.timeval = setInterval(() => {
      this.time -= 1;
      if (this.time<=0) {
          this.goHome()
      }
    }, 1000);

    this.$store.state.bool = false;
  },
  // 生命周期钩子，最后销毁函数
  destroyed() {
    // 当组件被销毁时候，修改导航数据
    this.$store.state.bool = true;
    clearInterval(this.timeval)
  }
};
</script>

<style lang="scss" scoped>
.bgbox {
  height: 100vh;
  background: #fde72f;
  line-height: 100vh;
  img {
    width: 100%;
    vertical-align: middle;
  }
}
.minibtn {
  position: absolute;
  right: 10px;
  top: 10px;
  background: rgba(0, 0, 0, 0.4);
  border-color: #ccc;
}
</style>